.swiper-button {
  &-next, &-prev {
    @extend %flex_center;
    width: 84px !important;
    height: 84px !important;
    border-radius: 25px;
    background: #D9787B;
    transition:  $transition;

    &:hover {
      background: #ad5f61;
    }

    @include breakpoint($lg-max) {
      display: none!important;
    }

    svg {
      width: 16px !important;
    }

    &:after {
      display: none;
    }
  }
}